import config from "../config";

export const formatPrice = (price) =>
  (Math.round(price * 100 + Number.EPSILON) / 100).toFixed(2) +
  "\xa0" +
  getCurrencySymbol(config.currency);

export const getCurrencySymbol = (currency) => {
  switch (currency) {
    case "EUR":
      return "";
    case "USD":
      return "$";
    default:
      return "";
  }
};

export const blobToBase64 = (blob, callBack, name) => {
  if (blob === null || blob === undefined) return;

  try {
    var reader = new FileReader();
    reader.onloadend = async () => {
      callBack(name, reader.result);
    };
    reader.readAsDataURL(blob);
  } catch (e) {
    console.error(e);
  }
};
