import React from "react";
import { withStyles } from "@material-ui/core/styles";
import getSharedStyles from "../../style";
import classNames from "classnames";
import { withLocalization } from "../shared/localization";
import withDoby from "../../consumers/withDoby";
import compose from "../../compose";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  desktopIntro: {
    height: "100vh",
    width: "100%",
    display: "flex",
    "justify-content": "center",
    position: "relative",
    // padding: "80px 0",
    "&:after": {
      content: "''",
      display: "block",
      position: "absolute",
      bottom: "0",
      left: "0",
      width: "100%",
      height: "100%",
      background: theme.palette.secondary.main,
      zIndex: -1,
    },
  },
  desktopIntroImageHolder: {
    width: "180px",
  },
  textAlignCenter: {
    "text-align": "center",
  },
  imageFit: {
    top: 0,
    left: 0,
    width: "100%",
    height: "100%",
    "z-index": 0,
    position: "relative",
  },
});

class DobyTatryChanged extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      seconds: 5,
    };
  }

  componentDidMount() {
    this.myInterval = setInterval(() => {
      const { seconds } = this.state;

      if (seconds > 0) {
        this.setState(({ seconds }) => ({
          seconds: seconds - 1,
        }));
      }

      if (seconds === 0) {
        clearInterval(this.myInterval);
        window.location.replace("https://dobyhory.sk");
      }
    }, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.myInterval);
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classNames(classes.desktopIntro, classes.textWhite)}>
        <div
          className={classNames(
            classes.dFlex,
            classes.flexCol,
            classes.alignItemsCenter,
            classes.justifyContentCenter
          )}
        >
          {/* DOBY TATRY - DOBY HORY IMAGE  */}
          <div
            className={classNames(
              classes.desktopIntroImageHolder,
              classes.imageFitHolder
            )}
          >
            <img
              alt="logo"
              src={"/img/doby-tatry-doby-hory.png"}
              className={classNames(classes.imageFit)}
            />
          </div>

          <br />
          <br />
          <div
            className={classNames(classes.textMono, classes.textAlignCenter)}
          >
            Doby Tatry sa zmenilo <br />
            na Doby Hory.
          </div>

          <br />
          <br />
          <div
            className={classNames(classes.textMono, classes.textAlignCenter)}
          >
            Priprav sa na turistick vzvy <br />
            <span style={{ color: "red" }}>po celom slovensku</span>
            <br />o {this.state.seconds}s
          </div>

          <br />
          <div
            className={classNames(classes.textMono, classes.textAlignCenter)}
          >
            alebo klikni na tento odkaz:{" "}
            <a href="https://dobyhory.sk" style={{ color: "white" }}>
              dobyhory.sk.
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default compose(
  withStyles(styles),
  withLocalization,
  withDoby
)(DobyTatryChanged);
