import React from 'react';
import PropTypes from 'prop-types';

import { withRouter } from 'react-router';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../shared/localization'

//import config from '../../config';
import getSharedStyles from '../../style';
import compose from '../../compose';
import withDoby from '../../consumers/withDoby';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import classNames from 'classnames';
//import Select from '../shared/Select';
import Button from '../dobyComponents/form/Button';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Grid from '@material-ui/core/Grid';
//import LinearProgress from '@material-ui/core/LinearProgress';
//import ItemCard from './ItemCard';
import RollerModal from './RollerModal';
import ProductSmallCard from '../dobyComponents/eshop/ProductSmallCard';
import BackpackAdd from '../dobyComponents/icons/BackpackAdd'
import Markdown from "../shared/Markdown";
import CircleLoader from '../dobyComponents/CircleLoader'

import { formatPrice/*, getCurrencySymbol*/ } from '../../utils/helpers';

const styles = theme => ({
    ...getSharedStyles(theme),
    root: {
        backgroundColor: '#f7f7f8'
    },
    imageContainer: {
        width: '100%',
        height: '300px',
        overflow: 'hidden',
        position: 'relative',
        //background: 'red',
        // margin: -2 * theme.spacing.unit
    },
    productImage: {
        position: 'absolute',
        width: '100%',
        height: '100%',
        top: '0',
        left: '0',
        objectFit: 'contain'
    },
    productVariantBtn: {
        padding: '8px 14px',
        minWidth: '43px',
        textAlign: 'center',
        border: `2px solid ${theme.palette.secondary.main}`,
    },
    productVariantBtnSelected: {
        background: theme.palette.secondary.main,
        color: 'white',
    },
    '@media screen and (max-width: 330px)': {
        productSmallCardHolder: { 
            maxWidth: '100%', 
            flexBasis: '100%'
        },
    },  
    '@media screen and (min-width: 331px)': {       
        productSmallCardHolder: {           
            "&:nth-of-type(2n+1)": {
                paddingRight: "2px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "2px",
            },
        }, 
    },
    '@media screen and (min-width: 430px)': {       
        productSmallCardHolder: {           
            "&:nth-of-type(2n+1)": {
                paddingRight: "5px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "5px",
            },
        }, 
    },
});

const _defaultState = {
    detailImage: null,
    selectedVariants: {},
    selectedProduct: null,
    detailsModalOpened: false,
    showSnackbar: false,
    lastLocation: window.location,
    defaultProduct: null,
    detailLoading: true,
};

class ProductDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = _defaultState;
    }

    componentDidMount() {
        this.fetchData();
    }

    componentDidUpdate() {
        if (this.state.lastLocation !== window.location.href) {
            this.setState({ ..._defaultState, lastLocation: window.location.href });
            this.fetchData();
        }
    }

    fetchDetailImage = async () => {
        if (!this.state.defaultProduct) return;

        const { imageId } = this.state.defaultProduct;
        if (imageId) {
            const data = await this.props.store.getFile(imageId);
            let reader = new FileReader();
            reader.onloadend = () => this.setState({ detailImage: reader.result });
            reader.readAsDataURL(data);
        }
    }

    fetchData = async () => {
        
        const _id  = this.props.inModal ? this.props.catalogId : this.props.match.params._id;
        await this.props.doby.getProductsByCatalogId(_id);

        const filter = [{ field: "main", value: true }];
        await this.props.doby.getProducts(filter);

        const catalogProducts = this.props.doby.catalog.data;

        if(catalogProducts[0]) {
            this.setState({ defaultProduct: catalogProducts[0] },
                () => this.fetchDetailImage()
           );
        }

        //NOTE: otherwise we are selecting first available parameter
        var defaultSelectedVariants = {};        
        for (let variantKey in this.props.doby.productVariants.data) { 
            const { data } = this.props.doby.productVariants.data[variantKey];
            
            if(!this.state.selectedVariants[variantKey] && data.length) {
                defaultSelectedVariants[variantKey] = data[0]._id;
            }
        }

        this.setState((state) => { 
            return {selectedVariants: {...state.selectedVariants, ...defaultSelectedVariants}}
        }, () => {
            const product = this.getProductByParametersSelection();            
            this.setState({ selectedProduct: product, detailLoading: false });
        });
    }

    handleChange = (name, value) => this.setState({ [name]: value });

    handleChangeVariant = (name, value) => {
        this.setState((state) => {
            return {
                ...state,
                selectedVariants: {
                    ...state.selectedVariants,
                    [name]: value
                }
            };
        }, () => {
            const product = this.getProductByParametersSelection();
            this.setState({ selectedProduct: product }, () => this.fetchDetailImage());
        });
    }

    handleProductImageLoaded = (e) => {
        let width = e.target.naturalWidth;
        let height = e.target.naturalHeight;
        if (height < width) {
            e.target.style.height = '100%';
            e.target.style.width = 'auto';
        } else {
            e.target.style.width = '100%';
            e.target.style.height = 'auto';
        }
    }

    renderVariants = () => {
        const {classes} = this.props;
        const generateVariant = (variantKey) => {
            const { data } = this.props.doby.productVariants.data[variantKey];
            const selected = this.state.selectedVariants[variantKey] ? this.state.selectedVariants[variantKey].toString() : '';
            return (
                <div className={classNames(classes.mb2)} key={variantKey}>
                    <div className={classNames(classes.mb2, classes.textMono, classes.textM)}>
                        {this.props.translate(variantKey)}
                    </div>
                    <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                        { data.map((item, index) => {
                            return (
                                <div
                                    //disabled={selected === item._id}
                                    onClick={() => this.handleChangeVariant(variantKey, item._id)}
                                    className={classNames(classes.productVariantBtn, classes.mr2, classes.textMono, classes.fontSemibold, selected === item._id && classes.productVariantBtnSelected )}
                                    key={index}
                                >
                                    {item.name}
                                </div>
                            )
                        })}  
                    </div>                 
                </div>
            )
        };

        const keys = Object.keys(this.props.doby.productVariants.data);        
        return (<>{keys.map((key) => generateVariant(key))}</>);
    }

    getProductByParametersSelection = () => {
        //NOTE: musme vybrat tolko dropdownov kolko mame unikatnych parametrov pre produkt variant
        if (Object.keys(this.state.selectedVariants).length !== Object.keys(this.props.doby.productVariants.data).length)
            return null;

        for (let variant of this.props.doby.catalog.data) {
            let hasAllRequirements = true;
            for (let variantKey in this.state.selectedVariants) {
                if (variant.tech_param && variant.tech_param[variantKey] !== this.state.selectedVariants[variantKey]) {
                    hasAllRequirements = false;
                    break;
                }
            }

            if (hasAllRequirements)
                return variant
        }
        return null;
    }

    addToCart = () => {
        if (this.state.selectedProduct) {
            this.props.doby.cartAddProduct(this.state.selectedProduct);
            this.setState({ showSnackbar: true });
        }
    }

    showProductDetail = (product) => {
        if (product.catalog_id && product.catalog_id.length > 0)
            this.props.history.push(`/eshop/product/${product.catalog_id}`);
    }

    renderSimillarProducts = () => {
        const { products } = this.props.doby;
        const {classes} = this.props;

        let uniqueProducts = [];      
        products.data.forEach(product => {
            let found = false;
            uniqueProducts.forEach(item=>{
                if(product.catalog_id === item.catalog_id) {
                    found = true;
                    return;
                }
            })
            if(!found) uniqueProducts.push(product)
        })

        return uniqueProducts.map((product) => {
                return (
                    <Grid item xs={6} sm={3} lg={2} key={product._id} className={classNames(classes.productSmallCardHolder, classes.mb3)}>
                        <ProductSmallCard
                            key={product._id}
                            title={product.name}
                            price={product.price_with_tax}
                            thumbnailId={product.thumbnailId}
                            catalogId={product.catalog_id}
                            onClickViewDetails={()=> this.showProductDetail()}
                        />                        
                    </Grid>
                );
            })
    }

    showAllProductDetails = () => this.setState({ detailsModalOpened: true });
    closeDetailsModal = () => this.setState({ detailsModalOpened: false });

    renderProductDetail = () => {
        const { classes } = this.props;
        const price = this.state.selectedProduct ? formatPrice(this.state.selectedProduct.price_with_tax) : ''
        return (
            <>   
                { this.state.detailLoading &&
                    <CircleLoader center className={classNames(classes.py8)}/>
                }             
                { !this.state.detailLoading && <div className={classNames(classes.container, !this.props.inModal && classes.mt5)}>
                    <div className={classes.imageContainer}>
                        <img
                            className={classes.productImage}
                            src={this.state.detailImage ? this.state.detailImage : ''}
                            //onLoad={(e) => { this.handleProductImageLoaded(e) }}
                            alt={''}
                        />
                    </div>
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                    >
                        <Grid item xs={12} className={classNames()}>
                            <h4 className={classNames(classes.uppercase, classes.mt5)}>{this.state.selectedProduct ? this.state.selectedProduct.name : this.state.defaultProduct ? this.state.defaultProduct.name : ''}</h4>

                            <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.justifyContentBetween, classes.mt4)}>
                                <div className={classNames(classes.textMono, classes.fontSemibold)}>
                                    {price} 
                                    {this.state.selectedProduct && <span className={classNames(classes.textM, classes.textGray400, classes.fontRegular, classes.ml2)}>
                                        {this.props.translate('with_tax')}
                                    </span>}
                                </div>
                                <div className={classes.productStock}>
                                    {this.state.selectedProduct ? this.state.selectedProduct.count > 0 ? this.props.translate('in_stock') : this.props.translate('out_stock') : ''}
                                </div>
                            </div>

                            <div className={classNames(classes.mt4)}>
                                {this.renderVariants()}
                            </div>

                            <Button
                                className={classNames(classes.mt4, classes.mb3)}
                                onClick={this.addToCart}
                                disabled={!this.state.selectedProduct || (this.state.selectedProduct && this.state.selectedProduct.disabled)}
                                fullwidth
                            >
                                {this.props.translate('add_to_cart')} <BackpackAdd size={30} className={classNames(classes.textWhite, classes.ml2)}/>
                            </Button>
                        </Grid>

                        <Grid item xs={12} className={classNames()}>
                            <div className={classNames(classes.mt4, classes.mb8)}>
                                <Markdown>{this.state.selectedProduct ? this.state.selectedProduct.long_description : ''}</Markdown>
                            </div>
                        </Grid>
                    </Grid>                              
                </div>}

                { !this.props.inModal && <div className={classNames(classes.bgLight)}>
                    <div className={classNames(classes.container)}>
                        <Grid
                            container
                            direction="row"
                            justify="flex-start"
                        >
                            <Grid item xs={12} className={classNames()}>
                                <h3 className={classNames(classes.uppercase, classes.mt7, classes.mb4)}>{this.props.translate('find_similar_products')}</h3>
                            </Grid>   
                        </Grid>
                        <Grid container className={classes.mt2}>
                            {this.renderSimillarProducts()}
                        </Grid>
                    </div>
                </div> }

                { <RollerModal
                    onClose={this.closeDetailsModal}
                    opened={this.state.detailsModalOpened}
                />}
            </>
        );
    }

    handleClose = () => this.setState({ showSnackbar: false });
    render() {
        return (
            <>
                { this.renderProductDetail() }

                <Snackbar
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.showSnackbar}
                    autoHideDuration={6000}
                    onClose={this.handleClose}
                    message={this.props.translate('successfully_added_to_cart')}
                    action={
                        <React.Fragment>
                            <IconButton size="small" aria-label="close" color="inherit" onClick={this.handleClose}>
                                <CloseIcon fontSize="small" />
                            </IconButton>
                        </React.Fragment>
                    }
                />
            </>
        );
    }
}

ProductDetail.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(
    withRouter,
    withStyles(styles),
    withDoby,
    withStore,
    withUser,
    withLocalization
)(ProductDetail);