import React from "react";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
// import IconButton from '@material-ui/core/IconButton';
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
// import Toolbar from '@material-ui/core/Toolbar';
// import Typography from '@material-ui/core/Typography';
// import AccountCircle from '@material-ui/icons/AccountCircle';
// import MenuItem from '@material-ui/core/MenuItem';
// import Menu from '@material-ui/core/Menu';

import { withStyles } from "@material-ui/core/styles";
import withWidth, { isWidthDown } from "@material-ui/core/withWidth";
import { withRouter } from "react-router";
import compose from "../../compose";
import { withLocalization } from "./localization";
import classNames from "classnames";
import getSharedStyles from "../../style";

import BackpackIcon from "../dobyComponents/icons/Backpack";
import MenuIcon from "../dobyComponents/icons/HamburgerMenu";
import NotificationIcon from "../dobyComponents/icons/Notification";
import withDoby from "../../consumers/withDoby";
import withMail from "../../consumers/withMail";

//const drawerWidth = 240;

const styles = (theme) => ({
  ...getSharedStyles(theme),
  root: {
    display: "flex",
  },
  grow: {
    flexGrow: 1,
  },
  drawer: {
    // [theme.breakpoints.up('lg')]: {
    //     width: drawerWidth,
    //     flexShrink: 0,
    // },
  },
  appBar: {
    background: "#fff",
    height: "60px",
    //marginLeft: drawerWidth,
    // [theme.breakpoints.up('lg')]: {
    //     width: `calc(100% - ${drawerWidth}px)`,
    // },
    boxShadow: "0px 0px 17px 1px rgba(0,0,0,0.08)",
  },
  menuIconHolder: {
    background: theme.palette.primary.main,
    height: "60px",
    width: "60px",
  },
  appBarLogoImg: {
    width: "140px",
  },
  appBarCol: {
    width: "calc((100% - 100px) / 2)",
  },
  menuButton: {
    marginRight: 20,
    [theme.breakpoints.up("lg")]: {
      display: "none",
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: "80%",
    maxWidth: "340px",
  },
  content: {
    flexGrow: 1,
    //padding: theme.spacing.unit * 2,
    width: "100%",
    paddingTop: "60px",
  },
  backpackCount: {
    position: "absolute",
    color: "white",
    background: theme.palette.primary.main,
    borderRadius: "20px",
    padding: "2px 5px",
    fontSize: "12px",
    minWidth: "19px",
    textAlign: "center",
    top: "-3px",
    right: "-2px",
  },
});

class ResponsiveDrawer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      mobileOpen: false,
      anchorEl: null,
    };
  }

  handleMenu = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  handleClickAccount = () => {
    this.props.history.push("/account");
    this.handleClose();
  };

  handleDrawerToggle = () => {
    this.setState((state) => ({ mobileOpen: !state.mobileOpen }));
  };

  redirect = (route) => {
    if (isWidthDown("md", this.props.width)) {
      this.handleDrawerToggle();
    }

    this.props.history.push(route.path);
  };

  getCartProductCount = () => {
    const { cart } = this.props.doby;
    return cart.data.products.length;
  };

  getNotificationsCount = () => {
    const { mails } = this.props.mail;

    console.log(mails);
    const unreadMails = mails.data.filter((m) => m.read === null);
    return unreadMails.length;
  };

  render() {
    const { classes, theme, routes, user, translate } = this.props;
    // const { anchorEl } = this.state;
    // const open = Boolean(anchorEl);
    const path = this.props.history.location.pathname;
    const route = routes.find((x) => x.path === path);

    const cartItems = this.getCartProductCount();
    const notificationItems = this.getNotificationsCount();

    const drawer = (
      <div>
        <div className={classes.toolbar} />
        <Divider />
        <List>
          {routes.map((route) => {
            if (
              route.exact &&
              !route.hidden &&
              (!route.permission || user.hasPermission(route.permission))
            )
              return (
                <ListItem
                  button
                  key={route.name}
                  onClick={() => this.redirect(route)}
                >
                  <ListItemText
                    primary={translate("route." + route.name) || route.name}
                  />
                </ListItem>
              );
            else return null;
          })}
        </List>
      </div>
    );

    if (!route || !route.anonymous || route.name === "home") {
      return (
        <div className={classes.root}>
          <CssBaseline />
          <AppBar position="fixed" className={classes.appBar} color="secondary">
            <div
              className={classNames(
                classes.dFlex,
                classes.alignItemsCenter,
                classes.h100,
                classes.mr3
              )}
            >
              {this.props.user.user ? (
                <>
                  <div
                    onClick={this.handleDrawerToggle}
                    className={classNames(
                      classes.menuIconHolder,
                      classes.dFlex,
                      classes.alignItemsCenter,
                      classes.justifyContentCenter,
                      classes.mr4
                    )}
                  >
                    <MenuIcon
                      className={classNames(classes.textWhite)}
                      size={33}
                    />
                  </div>
                  <img
                    src={this.props.logo}
                    className={classes.appBarLogoImg}
                    alt={""}
                  />
                  <div
                    className={classNames(
                      classes.mlAuto,
                      classes.dFlex,
                      classes.alignItemsCenter,
                      classes.justifyContentEnd
                    )}
                  >
                    <div
                      className={classNames(
                        classes.backpackHolder,
                        classes.dFlex,
                        classes.alignItemsCenter,
                        classes.positionRelative
                      )}
                    >
                      <NotificationIcon
                        className={classNames(
                          classes.textSecondary,
                          classes.mr2
                        )}
                        onClick={() =>
                          this.props.history.push("/notifications")
                        }
                        size={33}
                      />
                      {notificationItems > 0 && (
                        <div className={classNames(classes.backpackCount)}>
                          {notificationItems}
                        </div>
                      )}
                    </div>

                    <div
                      className={classNames(
                        classes.backpackHolder,
                        classes.dFlex,
                        classes.alignItemsCenter,
                        classes.positionRelative
                      )}
                    >
                      <BackpackIcon
                        className={classNames(classes.textSecondary)}
                        onClick={() => this.props.history.push("/eshop/cart")}
                        size={33}
                      />
                      {cartItems > 0 && (
                        <div className={classNames(classes.backpackCount)}>
                          {cartItems}
                        </div>
                      )}
                    </div>
                  </div>
                </>
              ) : (
                <>
                  <div className={classes.appBarCol}></div>
                  <img
                    src={this.props.logo}
                    className={classes.appBarLogoImg}
                    alt={""}
                    onDoubleClick={() => this.props.history.push("/login")}
                  />
                </>
              )}
            </div>
          </AppBar>
          <nav className={classes.drawer}>
            {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
            <Hidden lgUp implementation="css">
              <Drawer
                variant="temporary"
                anchor={theme.direction === "rtl" ? "right" : "left"}
                open={this.state.mobileOpen}
                onClose={this.handleDrawerToggle}
                classes={{
                  paper: classes.drawerPaper,
                }}
              >
                {drawer}
              </Drawer>
            </Hidden>
            {/* <Hidden mdDown implementation="css">
                            <Drawer
                                classes={{
                                    paper: classes.drawerPaper,
                                }}
                                variant="permanent"
                                open
                            >
                                {drawer}
                            </Drawer>
                        </Hidden> */}
          </nav>
          <main className={classes.content}>{this.props.children}</main>
        </div>
      );
    } else {
      return <div className={classes.root}>{this.props.children}</div>;
    }
  }
}

ResponsiveDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  children: PropTypes.arrayOf(PropTypes.element),
  user: PropTypes.object.isRequired,
  routes: PropTypes.array.isRequired,
};

export default compose(
  withWidth(),
  withStyles(styles, { withTheme: true }),
  withRouter,
  withLocalization,
  withDoby,
  withMail
)(ResponsiveDrawer);
