import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>           
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}
            >
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M21.5 20v4.5h-11V20l2.728-2v-1l-1-2-1-1v-2l1-1V9l2-2h1.5l1 .5h1.5l1.5 1.5v2l1 1v2l-1 1-1 2v1l2.772 2z"
                ></path>
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M14.26 24.5h-9v-3.729l2.233-1.657v-.828l-.818-1.657-.819-.829v-1.657l.819-.829v-1.657L8.31 10h1.227l.819.414h1.227M20 10.5l1.936-.5.818.414h1.227l1.227 1.243v1.657l.819.829V15.8l-.819.829-.818 1.657v.828l2.268 1.657V24.5h-9"
                ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);