import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import classNames from "classnames";

import { saveAs } from "file-saver";
import compose from "../../../compose";
import withUser from "../../../consumers/withUser";
import getSharedStyles from "../../../style";
import { Button } from "@material-ui/core";

import sk from "../../../localization/sk-SK.json";
import en from "../../../localization/en-US.json";
import cz from "../../../localization/cs-CZ.json";
import pl from "../../../localization/pl-PL.json";
import at from "../../../localization/de-AT.json";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  paper: {
    padding: theme.spacing.unit,
  },
  toMiddle: {
    height: "100vh",
    // display: "flex",
    // "justify-content": "center",
  },
});

const download = (json, name) => {
  const file = new Blob([JSON.stringify(json)], { type: "application/json" });
  saveAs(file, name + ".json");
};

function CenteredGrid(props) {
  const { classes } = props;
  return (
    <Grid container>
      <Grid item xs={12} className={classes.p2}>
        <div className={classNames(classes.toMiddle)}>
          <div
            className={classNames(
              classes.dFlex,
              classes.flexCol,
              classes.alignItemsCenter,
              classes.justifyContentCenter
            )}
          >
            <div
              className={classNames(classes.textMono, classes.textAlignCenter)}
            >
              sk-SK.json{" "}
              <Button
                variant="contained"
                color="primary"
                onClick={() => download(sk, "sk-SK")}
              >
                Stiahn
              </Button>
              <br />
              <br />
              en-US.json{" "}
              <Button
                variant="contained"
                color="primary"
                onClick={() => download(en, "en-US")}
              >
                Stiahn
              </Button>
              <br />
              <br />
              cs-CZ.json{" "}
              <Button
                variant="contained"
                color="primary"
                onClick={() => download(cz, "cs-CZ")}
              >
                Stiahn
              </Button>
              <br />
              <br />
              pl-PL.json{" "}
              <Button
                variant="contained"
                color="primary"
                onClick={() => download(pl, "pl-PL")}
              >
                Stiahn
              </Button>
              <br />
              <br />
              de-AT.json{" "}
              <Button
                variant="contained"
                color="primary"
                onClick={() => download(at, "de-AT")}
              >
                Stiahn
              </Button>
            </div>
          </div>
        </div>
      </Grid>
    </Grid>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(withStyles(styles), withUser)(CenteredGrid);
