import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function MenuIcon(props) {
    const { classes } = props;
    return (
        <>
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}>
                <path d="M4 8H28" stroke="currentColor" strokeWidth="1.5"/>
                <path d="M4 16H28" stroke="currentColor" strokeWidth="1.5"/>
                <path d="M4 24H28" stroke="currentColor" strokeWidth="1.5"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(MenuIcon);