import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{ height: `${props.size}px` }}
            >
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M9 16.5v-7L12.5 6h7L23 9.5v7L19.5 20h-7L9 16.5z"
                ></path>
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M13 14.5v-3l1.5-1.5h3l1.5 1.5v3L17.5 16h-3L13 14.5zM20 19.302V26l-4-2.06L12 26v-6.697"
                ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);