import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../../shared/localization";
import getSharedStyles from '../../../style';
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import classNames from "classnames";
import config from '../../../config';

const styles = (theme) => ({
    ...getSharedStyles(theme),
    checkbox: {
        color: `${theme.palette.secondary.main}!important`,
        padding: 0,
    },
    checkboxLight: {
        color: "white!important",
    },
    checkboxIcon: {
        fontSize: "24px",
    },
    controlNoLabel: {
        marginRight: "0",
    },
    formControlLabelRoot: {
        marginLeft: '0',
        marginRight: '0',
    },
    label: {
        marginLeft: '10px',     
        color: `${theme.palette.secondary.main}!important`,           
    },
    labelLight: {
        color: "white!important",
    },
    conditionsLink: {      
        textDecoration: 'underline',
        color: 'inherit'
    }
});

function CheckboxFieldConditionsComponent(props) {
    const { classes } = props;
    //const label = props.label || props.translate("field." + props.field);
    //const noLabel = props.noLabel ? props.noLabel : false;
    return (
        <FormControl className={classNames(props.className)} error={props.errorText !== null && props.errorText !== ""}>
            <FormControlLabel
                control={
                    <Checkbox
                        //value={props.value}
                        checked={props.value}
                        onChange={(e) => props.onChange(props.field, e.target.checked)}
                        classes={{ root: classNames(classes.checkbox, props.light && classes.checkboxLight) }}
                        icon={
                            <CheckBoxOutlineBlankIcon
                                className={classNames(classes.checkboxIcon)}
                            />
                        }
                        checkedIcon={
                            <CheckBoxIcon className={classNames(classes.checkboxIcon)} />
                        }
                        disabled={props.disabled}
                    />
                }
                label={
                    <div className={classNames()}>
                        <span>{props.translate('businessConditions.check')}</span>
                        <a href={`podmienky.${config.locale}.pdf`} rel="noreferrer" target="_blank" className={classNames(classes.conditionsLink, props.light && classes.textWhite)}>
                            {props.translate('businessConditions.conditions')}
                        </a>
                    </div>
                }
                classes={{ /*root: props.noLabel && classes.controlNoLabel,*/ root: classes.formControlLabelRoot, label: classNames(classes.label, props.labelClasses, classes.textMono, props.light && classes.labelLight) }}
            />             
            {props.errorText && <FormHelperText className={classNames(classes.textError)}>{props.errorText}</FormHelperText>}
        </FormControl>
    );
}

CheckboxFieldConditionsComponent.propTypes = {
    onChange: PropTypes.func.isRequired,
    field: PropTypes.string.isRequired,   
    value: PropTypes.bool,
    noLabel: PropTypes.bool,
    disabled: PropTypes.bool,
    labelClasses: PropTypes.string,
    light: PropTypes.bool,
};

export default withLocalization(withStyles(styles)(CheckboxFieldConditionsComponent));
