import { ajax } from "kd-web";
import config from "../config";
import { store } from "kd-web";
import * as helpers from "./helpers";

//-- [Summits] --
const SUMMITS_URL = config.url.doby + "/api/Summit";

export const addSummit = async (data) => {
  try {
    if (data._img) {
      const res = await store.postFile(data._img);
      data.imageId = res._id;
      data._img = null;
    }
    if (data._thumbnail) {
      const res = await store.postFile(data._thumbnail);
      data.thumbnailId = res._id;
      data._thumbnail = null;
    }
    await ajax.post(SUMMITS_URL, data);
  } catch (error) {
    throw error;
  }
};

export const getSummit = async (id) => {
  try {
    return await ajax.getById(SUMMITS_URL, id);
  } catch (error) {
    throw error;
  }
};

export const editSummit = async (data) => {
  try {
    await ajax.put(SUMMITS_URL, data, data._id);
  } catch (error) {
    throw error;
  }
};

export const getSummits = async (filters, sort, page) => {
  try {
    return ajax.get(SUMMITS_URL, filters, sort, page);
  } catch (error) {
    throw error;
  }
};

export const deleteSummit = async (id) => {
  try {
    await ajax.delById(SUMMITS_URL, id);
  } catch (error) {
    throw error;
  }
};
//-- [Challenges] --
const CHALLENGES_URL = config.url.doby + "/api/Challenge";
export const postFilesChallenge = async (c, inEditMode = false) => {
  let res;
  if (c._img) {
    if (!inEditMode || (inEditMode && c._imgChanged)) {
      res = await store.postFile(c._img);
      c.imageId = res._id;
    }
    c._img = null;
    c._imgChanged = null;
  }

  if (c._map) {
    if (!inEditMode || (inEditMode && c._mapChanged)) {
      res = await store.postFile(c._map);
      c.mapId = res._id;
    }
    c._map = null;
    c._mapChanged = null;
  }

  if (c._thumbnail) {
    if (!inEditMode || (inEditMode && c._thumbnailChanged)) {
      res = await store.postFile(c._thumbnail);
      c.thumbnailId = res._id;
    }
    c._thumbnail = null;
    c._thumbnailChanged = null;
  }

  if (c._logo) {
    if (!inEditMode || (inEditMode && c._logoChanged)) {
      res = await store.postFile(c._logo);
      c.logoId = res._id;
    }
    c._logo = null;
    c._logoChanged = null;
  }

  if (c._badge) {
    if (!inEditMode || (inEditMode && c._badgeChanged)) {
      res = await store.postFile(c._badge);
      c.badgeId = res._id;
    }
    c._badge = null;
    c._badgeChanged = null;
  }

  if (c._logoDark) {
    if (!inEditMode || (inEditMode && c._logoDarkChanged)) {
      res = await store.postFile(c._logoDark);
      c.logoDarkId = res._id;
    }
    c._logoDark = null;
    c._logoDarkChanged = null;
  }

  if (c._mapDark) {
    if (!inEditMode || (inEditMode && c._mapDarkChanged)) {
      res = await store.postFile(c._mapDark);
      c.mapDarkId = res._id;
    }
    c._mapDark = null;
    c._mapDarkChanged = null;
  }

  if (c._mapFull) {
    if (!inEditMode || (inEditMode && c._mapFullChanged)) {
      res = await store.postFile(c._mapFull);
      c.mapFullId = res._id;
    }
    c._mapFull = null;
    c._mapFullChanged = null;
  }

  if (c._mapFullTransparent) {
    if (!inEditMode || (inEditMode && c._mapFullTransparentChanged)) {
      res = await store.postFile(c._mapFullTransparent);
      c.mapFullTransparentId = res._id;
    }
    c._mapFullTransparent = null;
    c._mapFullTransparentChanged = null;
  }

  if (c._mapFullTransparent) {
    if (!inEditMode || (inEditMode && c._mapFullTransparentChanged)) {
      res = await store.postFile(c._mapFullTransparent);
      c.mapFullTransparentId = res._id;
    }
    c._mapFullTransparent = null;
    c._mapFullTransparentChanged = null;
  }

  if (c._medalFront) {
    if (!inEditMode || (inEditMode && c._medalFrontChanged)) {
      res = await store.postFile(c._medalFront);
      c.medalFrontId = res._id;
    }
    c._medalFront = null;
    c._medalFrontChanged = null;
  }

  if (c._medalBack) {
    if (!inEditMode || (inEditMode && c._medalBackChanged)) {
      res = await store.postFile(c._medalBack);
      c.medalBackId = res._id;
    }
    c._medalBack = null;
    c._medalBackChanged = null;
  }

  return c;
};

export const getFilesChallenge = async (
  c,
  callback = null,
  toBase64 = false
) => {
  if (c.imageId) c._img = await store.getFile(c.imageId);
  if (c.mapId) c._map = await store.getFile(c.mapId);
  if (c.thumbnailId) c._thumbnail = await store.getFile(c.thumbnailId);
  if (c.logoId) c._logo = await store.getFile(c.logoId);
  if (c.badgeId) c._badge = await store.getFile(c.badgeId);
  if (c.logoDarkId) c._logoDark = await store.getFile(c.logoDarkId);
  if (c.mapDarkId) c._mapDark = await store.getFile(c.mapDarkId);
  if (c.mapFullId) c._mapFull = await store.getFile(c.mapFullId);
  if (c.medalFrontId) c._medalFront = await store.getFile(c.medalFrontId);
  if (c.medalBackId) c._medalBack = await store.getFile(c.medalBackId);
  if (c.mapFullTransparentId)
    c._mapFullTransparent = await store.getFile(c.mapFullTransparentId);

  if (toBase64 && callback) {
    helpers.blobToBase64(c._img, callback, "_img");
    helpers.blobToBase64(c._map, callback, "_map");
    helpers.blobToBase64(c._thumbnail, callback, "_thumbnail");
    helpers.blobToBase64(c._logo, callback, "_logo");
    helpers.blobToBase64(c._badge, callback, "_badge");
    helpers.blobToBase64(c._logoDark, callback, "_logoDark");
    helpers.blobToBase64(c._mapDark, callback, "_mapDark");
    helpers.blobToBase64(c._mapFull, callback, "_mapFull");
    helpers.blobToBase64(c._medalFront, callback, "_medalFront");
    helpers.blobToBase64(c._medalBack, callback, "_medalBack");
    helpers.blobToBase64(
      c._mapFullTransparent,
      callback,
      "_mapFullTransparent"
    );
  }

  return c;
};

export const deleteFilesChallenge = async (c, inEditMode = false) => {
  if (c.imageId) {
    if (!inEditMode || (inEditMode && c._imgChanged)) {
      await store.delFile(c.imageId);
      c.imageId = null;
    }
  }

  if (c.mapId) {
    if (!inEditMode || (inEditMode && c._mapChanged)) {
      await store.delFile(c.mapId);
      c.mapId = null;
    }
  }

  if (c.thumbnailId) {
    if (!inEditMode || (inEditMode && c._thumbnaiChanged)) {
      await store.delFile(c.thumbnailId);
      c.thumbnailId = null;
    }
  }

  if (c.logoId) {
    if (!inEditMode || (inEditMode && c._logoChanged)) {
      await store.delFile(c.logoId);
      c.logoId = null;
    }
  }

  if (c.badgeId) {
    if (!inEditMode || (inEditMode && c._badgeChanged)) {
      await store.delFile(c.badgeId);
      c.badgeId = null;
    }
  }

  if (c.logoDarkId) {
    if (!inEditMode || (inEditMode && c._logoDarkChanged)) {
      await store.delFile(c.logoDarkId);
      c.logoDarkId = null;
    }
  }

  if (c.mapDarkId) {
    if (!inEditMode || (inEditMode && c._mapDarkChanged)) {
      await store.delFile(c.mapDarkId);
      c.mapDarkId = null;
    }
  }

  if (c.mapFullId) {
    if (!inEditMode || (inEditMode && c._mapFullChanged)) {
      await store.delFile(c.mapFullId);
      c.mapFullId = null;
    }
  }

  if (c.mapFullTransparentId) {
    if (!inEditMode || (inEditMode && c._mapFullTransparentChanged)) {
      await store.delFile(c.mapFullTransparentId);
      c.mapFullTransparentId = null;
    }
  }

  if (c.medalFrontId) {
    if (!inEditMode || (inEditMode && c._medalFrontChanged)) {
      await store.delFile(c.medalFrontId);
      c.medalFrontId = null;
    }
  }

  if (c.medalBackId) {
    if (!inEditMode || (inEditMode && c._medalBackChanged)) {
      await store.delFile(c.medalBackId);
      c.medalBackId = null;
    }
  }

  return c;
};

export const addChallenge = async (data) => {
  try {
    data = await postFilesChallenge(data);
    await ajax.post(CHALLENGES_URL, data);
  } catch (error) {
    throw error;
  }
};

export const getChallenge = async (id) => {
  try {
    let challenge = await ajax.getById(CHALLENGES_URL, id);
    if (challenge)
      challenge.summitsRefIds = challenge.summitsRef.map((x) => x.id);

    return challenge;
  } catch (error) {
    throw error;
  }
};

export const editChallenge = async (data) => {
  try {
    data = await deleteFilesChallenge(data, true);
    data = await postFilesChallenge(data, true);
    await ajax.put(CHALLENGES_URL, data, data._id);
  } catch (error) {
    throw error;
  }
};

export const getChallenges = async (filters, sort, page) => {
  try {
    return await ajax.get(CHALLENGES_URL, filters, sort, page);
  } catch (error) {
    throw error;
  }
};

export const deleteChallenge = async (id, data = null) => {
  try {
    await ajax.delById(CHALLENGES_URL, id);
    if (data) deleteFilesChallenge(data);
  } catch (error) {
    throw error;
  }
};

