import React from "react";
import { withRouter } from "react-router";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import classNames from "classnames";

import NotificationCard from "../dobyComponents/NotificationCard";
import compose from "../../compose";
import getSharedStyles from "../../style";
import { withLocalization } from "../shared/localization";
import withMail from "../../consumers/withMail";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";

const styles = (theme) => ({
  ...getSharedStyles(theme),
  title: {
    "font-size": "x-large",
  },
});

class Notifications extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      notifications: [],
    };
  }

  async componentDidMount() {
    const { getMails, readMail } = this.props.mail;

    await readMail("");
    await getMails();

    const { mails } = this.props.mail;

    this.setState((state) => ({
      notifications: mails.data,
    }));
    this.getSendersData();
  }

  getSendersData = async () => {
    const { notifications } = this.state;
    const { getUserCached } = this.props.user;

    notifications.map(async (n) => {
      const senderData = await getUserCached(n.senderRef.id);
      if (senderData && senderData.avatar)
        this.setAvatarToNotificationCallback(senderData.avatar, n._id);
    });
  };

  setAvatarToNotificationCallback = (avatar, id) => {
    this.setState((state) => ({
      notifications: state.notifications.map((n) => {
        if (n._id === id) return { ...n, avatar };
        return n;
      }),
    }));
  };

  renderNotifications = () => {
    const { classes } = this.props;

    return this.state.notifications.map((n) => {
      return (
        <NotificationCard
          className={classNames(classes.mt3)}
          avatar={n.avatar}
          onClick={() => this.showNotification(n.ru)}
          subject={n.subject}
          created={n.created}
        />
      );
    });
  };

  showNotification = (ru) => this.props.history.push(ru);

  render() {
    const { classes, translate } = this.props;
    return (
      <>
        <div>
          <Grid container direction="row" justify="center" alignItems="center">
            {/* HEADER */}
            <Grid item xs={12} className={classNames(classes.mt5)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(classes.textXs)}
                  onClick={this.goToProfile}
                >
                  {translate("back")}
                </div>
              </div>
            </Grid>

            {/* NOTIFICATIONS */}
            <Grid item xs={12} className={classNames(classes.mt7)}>
              <div className={classNames(classes.container)}>
                <div
                  className={classNames(
                    classes.dFlex,
                    classes.alignItemsCenter,
                    classes.justifyContentBetween
                  )}
                >
                  <div
                    className={classNames(classes.fontSemibold, classes.title)}
                  >
                    {translate("notifications")}
                  </div>
                </div>

                {this.renderNotifications()}
              </div>
            </Grid>
          </Grid>
        </div>
      </>
    );
  }
}

Notifications.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withDoby,
  withMail,
  withStore,
  withLocalization,
  withRouter,
  withUser
)(Notifications);
