import config from "../config";
import { mail, storage } from "kd-web";
import * as signalR from "@aspnet/signalr";

export const send = async (data) => {
  try {
    return await mail.send(data);
  } catch (error) {
    throw error;
  }
};

export const getMails = async (filters, sort, page) => {
  try {
    return mail.get(filters, sort, page);
  } catch (error) {
    throw error;
  }
};

export const read = async (id) => {
  try {
    return await mail.read(id);
  } catch (error) {
    throw error;
  }
};

export const sendMock = async () => {
  try {
    const data = {
      subject: "subject",
      body: "body",
      from: "mail@gmail.com",
      fromName: "mailer",
      recipientRef: {
        id: "5fef09fe7d1c7341543b71a5",
      },
      templateId: 1,
      params: ["<b>toto je test</b>"],
    };

    return await mail.send(data);
  } catch (error) {
    throw error;
  }
};

const getToken = () => {
  return storage.getUser().refreshToken;
};

const onConnect = (userId) => {
  console.log("hub_connect", userId);
};

const onDisconnect = (userId) => {
  console.log("hub_disconnect", userId);
};

export const initHub = (onNotification) => {
  const connection = new signalR.HubConnectionBuilder()
    .withUrl(`${config.url.mail}/hub`, { accessTokenFactory: getToken })
    .configureLogging(signalR.LogLevel.Error)
    .build();

  connection.start().catch((err) => {
    console.error("hub", err);
  });

  connection.on("connect", onConnect);
  connection.on("disconnect", onDisconnect);
  connection.on("notification", onNotification);
};
