import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import NumInput from "../shared/NumInput";
import FileInput from "../shared/FileInput";
import Switch from "../shared/Switch";
import Editor from "../shared/Editor";
import Select from "../shared/Select";
import config from "../../config";
import TechParamForm from "./TechParamForm";
import Grid from "@material-ui/core/Grid";

import classNames from "classnames";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import _ from "lodash";

const styles = (theme) => ({
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
  margin: {
    margin: theme.spacing.unit,
  },
  mr10: {
    "margin-right": "10px",
  },
  ml10: {
    "margin-left": "10px",
  },
  pr10: {
    "padding-right": "10px",
  },
  pl10: {
    "padding-left": "10px",
  },
});

const INIT_STATE = {
  name: "",
  catalog_id: "",
  ean: "",
  short_description: "",
  long_description: "",
  price_without_tax: 0,
  disabled: false,
  main: false,
  category: [],
  productsRef: [],
  _img: null,
  _thumbnail: null,
  tech_param: {},
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ...(props.data || INIT_STATE),
      tech_params_forms_states: [],
      enumTypes: [],
    };
  }

  async componentDidMount() {
    try {
      const { getEnums } = this.props.store;
      const { getSubordinateProducts } = this.props.doby;

      const filter = [
        { field: "enumTypeId", op: "gte", value: 200 },
        { field: "enumTypeId", op: "lt", value: 300 },
        { field: "disabled", value: false },
      ];

      await getEnums(filter);
      await getSubordinateProducts();

      this.setEnumTypes();
      this.setTechParamForms();
    } catch (e) {
      console.error(e);
    }
  }

  setTechParamForms = () => {
    const { tech_param } = this.state;

    if (!_.isEmpty(tech_param)) {
      for (const [enumTypeID, enumValueID] of Object.entries(tech_param))
        this.addTechParamForm(Number.parseInt(enumTypeID), enumValueID);
    }
  };

  setEnumTypes = () => {
    const { enumTypes } = this.props.store;
    const enumTypesFiltered = enumTypes.data.filter(
      (e) => e._id > 200 && e._id < 300
    );

    this.setState((state) => ({
      enumTypes: enumTypesFiltered,
    }));
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addProduct, refreshProducts, editProduct } = this.props.doby;

    const tech_param = {};
    for (const state of this.state.tech_params_forms_states)
      tech_param[state.enumType] = state.enumValue;

    try {
      if (this.state._id) {
        await editProduct({ ...this.state, tech_param });
      } else {
        await addProduct({ ...this.state, tech_param });
      }
      refreshProducts();
      this.setState({ ...INIT_STATE, tech_param_forms: [] });
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleChangeTechParamForm = (name, value, index) => {
    let tech_params_forms_states = [...this.state.tech_params_forms_states];

    tech_params_forms_states = tech_params_forms_states.map((state) => {
      return state.index === index ? { ...state, [name]: value } : state;
    });

    this.setState((state) => ({
      tech_params_forms_states,
    }));
  };

  handleAddTechParam = (event) => {
    event.preventDefault();
    this.addTechParamForm();
  };

  removeTechParamForm = (index) => {
    let tech_params_forms_states = [...this.state.tech_params_forms_states];
    let tech_param = { ...this.state.tech_param };

    const state = tech_params_forms_states.find(
      (state) => state.index === index
    );
    delete tech_param[state.enumType];

    tech_params_forms_states = tech_params_forms_states.filter(
      (state) => state.index !== index
    );
    tech_params_forms_states = tech_params_forms_states.map((state, index) => ({
      ...state,
      index,
    }));

    this.setState((state) => ({
      tech_param,
      tech_params_forms_states,
    }));
  };

  addTechParamForm = (enumType = null, enumValue = null) => {
    const tech_params_forms_states = [...this.state.tech_params_forms_states];
    const index = tech_params_forms_states.length;

    this.setState((state) => ({
      tech_params_forms_states: [
        ...tech_params_forms_states,
        {
          index,
          enumType,
          enumValue,
        },
      ],
    }));
  };

  render() {
    const { classes, disabled } = this.props;
    const { enums } = this.props.store;
    const { subordinateProducts } = this.props.doby;
    const {
      name,
      price_without_tax,
      category,
      productsRef,
      _img,
      _thumbnail,
      catalog_id,
      ean,
      short_description,
      long_description,
      tech_params_forms_states,
      enumTypes,
    } = this.state;

    const disabledAddTechParamForm =
      tech_params_forms_states.length === enumTypes.length;

    return (
      <>
        <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
          <Grid container direction="row" justify="left" alignItems="left">
            <Grid item xs={4} className={classNames(classes.pr10)}>
              <TextInput
                value={name}
                field="name"
                onChange={this.handleChange}
                required
                disabled={disabled}
                fullWidth
              />
            </Grid>
            <Grid item xs={5} className={classNames(classes.pr10)}>
              <TextInput
                value={short_description}
                field="short_description"
                onChange={this.handleChange}
                disabled={disabled}
                fullWidth
              />
            </Grid>
            <Grid item xs={3} className={classNames(classes.pr10)}>
              <TextInput
                value={ean}
                field="ean"
                onChange={this.handleChange}
                disabled={disabled}
                fullWidth
              />
            </Grid>
          </Grid>

          <Grid container direction="row" justify="left" alignItems="left">
            <Grid item xs={3} className={classNames(classes.pr10)}>
              <TextInput
                value={catalog_id}
                field="catalog_id"
                onChange={this.handleChange}
                required
                disabled={disabled}
                fullWidth
              />
            </Grid>
            <Grid item xs={4} className={classNames(classes.pr10)}>
              <div className={classes.nameTextInput}>
                <Select
                  value={category ? category : []}
                  field="category"
                  onChange={this.handleChange}
                  data={enums.data.filter((e) => e.enumTypeId === 200)}
                  textField="name"
                  multiple
                  fullWidth
                  disabled={disabled}
                />
              </div>
            </Grid>
            <Grid item xs={5} className={classNames(classes.pr10)}>
              <div className={classes.nameTextInput}>
                <Select
                  value={productsRef ? productsRef : []}
                  field="productsRef"
                  onChange={this.handleChange}
                  data={subordinateProducts.data}
                  textField="name"
                  multiple
                  fullWidth
                  disabled={disabled}
                />
              </div>
            </Grid>
          </Grid>

          <NumInput
            value={price_without_tax}
            field="price_without_tax"
            onChange={this.handleChange}
            min={0}
            required
          />

          <Switch
            label="Zruen"
            value={this.state.disabled}
            field="disabled"
            onChange={this.handleChange}
          />
          <Switch
            label="Na hlavnej strnke"
            value={this.state.main}
            field="main"
            onChange={this.handleChange}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_img}
            field="_img"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.imageSize}
          />
          <FileInput
            mimeType="image/jpeg"
            value={_thumbnail}
            field="_thumbnail"
            onChange={this.handleChange}
            disabled={this.state._id || this.state._id === "" || disabled}
            height={config.thumbnailSize}
          />
        </Form>

        <Editor
          field="long_description"
          value={long_description}
          onChange={this.handleChange}
          height={config.imageSize}
          disabled={disabled}
        />

        {tech_params_forms_states.map((state) => {
          const { enumType, enumValue, index } = state;
          return (
            <TechParamForm
              classe={classes}
              onChange={this.handleChangeTechParamForm}
              onDelete={this.removeTechParamForm}
              enumTypes={enumTypes}
              enumType={enumType}
              enumValue={enumValue}
              index={index}
            />
          );
        })}

        <Button
          disabled={disabledAddTechParamForm}
          onClick={this.handleAddTechParam}
          className={classes.button}
        >
          + Prida technicky parameter
        </Button>
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(FormWrapper);
