import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../../shared/TextInput";
import Select from "../../shared/Select";
import Form from "../../shared/form/Form";
import DatePicker from "../../shared/DatePicker";
import FileInput from "../../shared/FileInput";
import NumInput from "../../shared/NumInput";
import { getStyles } from "../../shared/style";
import config from "../../../config";
import Grid from "@material-ui/core/Grid";

import classNames from "classnames";
import withDoby from "../../../consumers/withDoby";
import withUser from "../../../consumers/withUser";
import withStore from "../../../consumers/withStore";
import compose from "../../../compose";

const styles = (theme) => ({
  ...getStyles(theme),
  nameTextInput: {
    display: "flex",
    flex: 1,
  },
  mr10: {
    "margin-right": "10px",
  },
  ml10: {
    "margin-left": "10px",
  },
  pr10: {
    "padding-right": "10px",
  },
  pl10: {
    "padding-left": "10px",
  },
});

const INIT_STATE = {
  name: "",
  description: "",
  summitsRef: [],
  serieRef: null,
  difficulty: 1,
  start: new Date(),
  end: new Date(),
  key: 0,
  _img: null,
  _thumbnail: null,
  _map: null,
  _logo: null,
  _badge: null,
  _mapDark: null,
  _mapFull: null,
  _mapFullTransparent: null,
  _logoDark: null,
  _medalFront: null,
  _medalBack: null,
};

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = props.data || INIT_STATE;
  }

  async componentDidMount() {
    try {
      const { getSummits, getSeries, getFilesChallenge } = this.props.doby;

      await getSummits();
      await getSeries();

      if (this.state._id)
        getFilesChallenge(this.state, this.handleChange, true);
    } catch (e) {
      console.error(e);
    }
  }

  handleChangeFile = (name, value) => {
    this.setState({ [name]: value, [name + "Changed"]: true });
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleChangeSummitsRef = (name, value) => {
    const v = value.map((x) => ({ id: x }));
    this.setState({ [name]: v });
  };

  handleChangeSerieRef = (name, value) => {
    this.setState({ [name]: { id: value } });
  };

  handleSubmit = async () => {
    const { addChallenge, refreshChallenges, editChallenge } = this.props.doby;

    try {
      if (this.state._id) {
        await editChallenge(this.state);
      } else {
        await addChallenge(this.state);
      }

      refreshChallenges();
      this.setState(INIT_STATE);

      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      name,
      description,
      start,
      end,
      summitsRef,
      serieRef,
      difficulty,
      key,
      _img,
      _map,
      _thumbnail,
      _logo,
      _badge,
      _mapDark,
      _mapFull,
      _logoDark,
      _mapFullTransparent,
      _medalFront,
      _medalBack,
    } = this.state;
    const { summits, series } = this.props.doby;

    return (
      <Form onSubmit={this.handleSubmit} inline disabled={disabled}>
        <Grid container direction="row" justify="left" alignItems="left">
          <Grid item xs={6} className={classNames(classes.pr10)}>
            <TextInput
              value={name}
              field="name"
              onChange={this.handleChange}
              required
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs={3} className={classNames(classes.pr10)}>
            <DatePicker
              value={start}
              field="start"
              onChange={this.handleChange}
              required
            />
          </Grid>
          <Grid item xs={3} className={classNames(classes.pr10)}>
            <DatePicker
              value={end}
              field="end"
              onChange={this.handleChange}
              required
            />
          </Grid>
        </Grid>

        <Grid container direction="row" justify="left" alignItems="left">
          <Grid item xs={12} className={classNames(classes.pr10)}>
            <TextInput
              value={description}
              field="description"
              onChange={this.handleChange}
              required
              fullWidth
              disabled={disabled}
            />
          </Grid>
        </Grid>

        <Grid container direction="row" justify="left" alignItems="left">
          <Grid item xs={2} className={classNames(classes.pr10)}>
            <NumInput
              value={key}
              field="key"
              onChange={this.handleChange}
              min={0}
              step={1}
              fullWidth
            />
          </Grid>

          <Grid item xs={2} className={classNames(classes.pr10)}>
            <NumInput
              value={difficulty}
              field="difficulty"
              onChange={this.handleChange}
              min={0}
              max={5}
              step={1}
              required
              fullWidth
            />
          </Grid>

          <Grid item xs={4} className={classNames(classes.pr10, classes.pl10)}>
            <Select
              value={serieRef ? serieRef.id : null}
              field="serieRef"
              onChange={this.handleChangeSerieRef}
              data={series.data}
              textField="name"
              required
              idField="id"
              disabled={disabled}
              fullWidth
            />
          </Grid>
          <Grid item xs={4} className={classNames(classes.pr10)}>
            <Select
              value={summitsRef.map((x) => x.id)}
              field="summitsRef"
              onChange={this.handleChangeSummitsRef}
              fullWidth
              data={summits.data}
              textField="name"
              required
              multiple
              idField="id"
              disabled={disabled}
            />
          </Grid>
        </Grid>

        <FileInput
          mimeType="image/jpeg"
          value={_img}
          field="_img"
          onChange={this.handleChangeFile}
          disabled={disabled}
          height={config.imageSize}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_thumbnail}
          field="_thumbnail"
          onChange={this.handleChangeFile}
          disabled={disabled}
          height={config.thumbnailSize}
        />

        <FileInput
          mimeType="image/png"
          value={_logo}
          field="_logo"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/png"
          value={_map}
          field="_map"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_badge}
          field="_badge"
          onChange={this.handleChangeFile}
          disabled={disabled}
          height={config.avatarSize}
        />

        <FileInput
          mimeType="image/png"
          value={_logoDark}
          field="_logoDark"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/png"
          value={_mapDark}
          field="_mapDark"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/jpeg"
          value={_mapFull}
          field="_mapFull"
          onChange={this.handleChangeFile}
          disabled={disabled}
          height={config.imageSize}
        />

        <FileInput
          mimeType="image/png"
          value={_mapFullTransparent}
          field="_mapFullTransparent"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/png"
          value={_medalFront}
          field="_medalFront"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />

        <FileInput
          mimeType="image/png"
          value={_medalBack}
          field="_medalBack"
          onChange={this.handleChangeFile}
          disabled={disabled}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool,
};

export default compose(
  withStyles(styles),
  withDoby,
  withUser,
  withStore
)(FormWrapper);
