import React from "react";
import MailContext from "../contexts/MailContext";
import * as mailUtils from "../utils/mailUtils";

class MailProvider extends React.Component {
  state = {
    mails: {
      total: 0,
      data: [],
      filters: [],
      page: null,
      isLoading: false,
    },
  };

  getMails = async (filters, sort, page, render = true) => {
    try {
      if (!render) {
        return await mailUtils.getMails(filters, sort, page);
      }

      this.setState((state) => ({
        mails: { ...state.mails, isLoading: true },
      }));
      const mails = await mailUtils.getMails(filters, sort, page);
      mails.isLoading = false;
      this.setState({ mails });
      return mails;
    } catch (error) {
      throw error;
    }
  };

  onNotification = () => {
    this.getMails();
  };

  initHub = () => {
    mailUtils.initHub(this.onNotification);
    this.getMails();

    setTimeout(async function () {
      //const res = await mailUtils.sendMock();
      // console.log(res);
    }, 2000);
  };

  render() {
    const { children } = this.props;

    return (
      <MailContext.Provider
        value={{
          getMails: this.getMails,
          initHub: this.initHub,
          sendMail: mailUtils.send,
          readMail: mailUtils.read,
          ...this.state,
        }}
      >
        {children}
      </MailContext.Provider>
    );
  }
}

export default MailProvider;
