import dev from './config.dev';
import sk from './config.sk';
import cz from './config.cz';
import at from './config.at';
import pl from './config.pl';
// import test from './config.test';

const config = {
  imageSize: 1080,
  thumbnailSize: 320,
  avatarSize: 168,
}

const getConfig = () => {
  if(process.env.NODE_ENV === 'development') {
    return dev;
  }

  switch(process.env.REACT_APP_HOMEPAGE) {
    case 'https://dobyhory.sk':
      return sk;

    case 'https://jdidohor.cz': 
      return cz;

    case 'https://bergsammler.at':
      return at;
      
    case 'https://zdobywajgory.pl': 
      return pl;

    case 'https://doby.krdeldatlov.sk':
      return dev;

    default:
      return dev;
  }
}

const c = { ...config, ...getConfig() };
export default c;