import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function StopwatchIcon(props) {
    const { classes } = props;
    return (
        <>           
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}>
                <path d="M7 22.5V13.5L11.5 9H20.5L25 13.5V22.5L20.5 27H11.5L7 22.5Z" stroke="currentColor" strokeWidth="1.5"/>
                <path d="M15.5 18.5L20 14" stroke="currentColor" strokeWidth="1.5" strokeLinejoin="round"/>
                <path d="M11 5L21 5" stroke="currentColor" strokeWidth="1.5" strokeLinejoin="round"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(StopwatchIcon);