import React from "react";
import { Route, Redirect, withRouter } from "react-router-dom";
import gtag, { install } from "ga-gtag";
import Helmet from "react-helmet";
import { withLocalization } from "./components/shared/localization";

import Snackbar from "@material-ui/core/Snackbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import withStyles from "@material-ui/core/styles/withStyles";

import compose from "./compose";
import withUser from "./consumers/withUser";
import withMail from "./consumers/withMail";
import Container from "./components/shared/Container";
import routes from "./routes";
import config from "./config";

const styles = (theme) => ({
  close: {
    padding: theme.spacing.unit / 2,
  },
});

let _init = false;
class App extends React.Component {
  constructor(props) {
    super(props);
    install(config.ga);
  }

  componentDidUpdate(prevProps) {
    if (this.props.location !== prevProps.location) {
      gtag("config", config.ga, { page_path: this.props.location.pathname });
    }
  }

  render() {
    const { user, mail, classes, translate } = this.props;
    const title = translate("title");
    const description = translate("description");

    if (!_init && user.user) {
      _init = true;
      mail.initHub();
    }

    return (
      <React.Fragment>
        <Helmet>
          <title>{title}</title>
          <meta name="description" content={description} />
        </Helmet>
        <Container
          user={user}
          routes={routes}
          logo={`/img/${config.locale}/logo.png`}
        >
          {routes.map((x) => {
            if (x.anonymous)
              return (
                <Route
                  key={x.name}
                  path={x.path}
                  exact={x.exact}
                  component={x.component}
                />
              );
            else
              return (
                <PrivateRoute
                  exact={x.exact}
                  key={x.name}
                  path={x.path}
                  component={x.component}
                  user={user}
                  permission={x.permission}
                />
              );
          })}
        </Container>

        {/* <BottomNavigationBar/> */}

        <Snackbar
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          open={user.msg}
          autoHideDuration={6000}
          onClose={user.hideMsg}
          ContentProps={{
            "aria-describedby": "message-id",
          }}
          message={<span id="message-id">{user.msg}</span>}
          action={[
            <IconButton
              key="close"
              aria-label="Close"
              color="inherit"
              className={classes.close}
              onClick={user.hideMsg}
            >
              <CloseIcon />
            </IconButton>,
          ]}
        />
      </React.Fragment>
    );
  }
}

function PrivateRoute({ component: Component, user, permission, ...rest }) {
  return (
    <Route
      {...rest}
      render={(props) => {
        if (!user.user) {
          return (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: props.location },
              }}
            />
          );
        } else if (permission && !user.hasPermission(permission)) {
          return (
            <Redirect
              to={{
                pathname: config.home,
                state: { from: props.location },
              }}
            />
          );
        } else {
          return <Component {...props} />;
        }
      }}
    />
  );
}

export default compose(
  withStyles(styles),
  withUser,
  withMail,
  withLocalization,
  withRouter
)(App);
