import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>          
            <svg width="100%" height="100%" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{ height: `${props.size}px` }}>
                <path d="M9 13.875L6 14.625L6 5.625L9 4.875L15 6.375L18 5.625V14.625L15 15.375L9 13.875Z" stroke="currentColor" stroke-width="1.3"/>
                <path d="M6 19.875L6 14.625" stroke="currentColor" stroke-width="1.3" strokeLinejoin="round"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);