import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function TShirtIcon(props) {
    const { classes } = props;
    return (
        <>
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}>
                <path d="M21.5 13.5V26H16H10.5V13.5L7 15.5L4.5 10L11.5 6L13.5 7.5L16 8L18.5 7.5L20.5 6L27.5 10L25 15.5L21.5 13.5Z" stroke="currentColor" stroke-width="1.5"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(TShirtIcon);