import React from 'react';
import { withRouter } from 'react-router';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withDoby from '../../consumers/withDoby';
import withUser from '../../consumers/withUser';
import { withLocalization } from '../shared/localization'
import { formatPrice } from '../../utils/helpers';

import Grid from '@material-ui/core/Grid';
import Button from '../dobyComponents/form/Button';
import CartItem from '../dobyComponents/eshop/CartItem';

import BackpackIcon from '../dobyComponents/icons/Backpack'
import TShirtIcon from '../dobyComponents/icons/TShirt'

const styles = theme => ({
    ...getSharedStyles(theme),
    emptyCartIcon: {
        height: '90px',
        width: 'auto',
    },
    emptyCartIconHolder: {
        width: '150px',
        height: '150px',
    },
    bottomSection: {
        marginTop: '20px',
        bottom: '0'
    },
    fixedBottomBar: {
        position: 'fixed',
        bottom: '0',
        left: '0',
        width: '100%',
        background: theme.palette.secondary.main,
        zIndex: 1,
    },
});

class Cart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        this.props.doby.cartGetProducts();
    }
    
    handleChange = (name, value) => {
        this.setState({ [name]: value });
    }
    
    getCartProductCount = () => {
        const { cart } = this.props.doby;
        return cart.data.products.length;
    }

    goToEshop = ()      => this.props.history.push(`/eshop`);
    goToCheckout = ()   => this.props.history.push('/eshop/checkout-delivery-payment');
    
    renderEmptyCart = ()=> {
        const { classes } = this.props;
        return ( 
            <div className={classNames(classes.dFlex, classes.flexCol, classes.alignItemsCenter)}>
                <div className={classNames(classes.mt8, classes.textCenter)}>
                    <div className={classNames(classes.emptyCartIconHolder, classes.bgLight, classes.dFlex, classes.alignItemsCenter, classes.justifyContentCenter)}>
                        <BackpackIcon className={classNames(classes.emptyCartIcon)}/>
                    </div>
                </div>
                
                <h5 className={classNames(classes.textCenter, classes.mt9)}>
                    { this.props.translate('cart_is_empty1') }
                </h5>

                <div className={classNames(classes.textMono, classes.fontMedium, classes.textCenter, classes.mt3)}>
                    { this.props.translate('cart_is_empty2') }
                </div>

                <Button 
                    className={classNames(classes.mt8)}
                    onClick={this.goToEshop}
                    fullwidth
                    outline                    
                >
                    { this.props.translate('go_to_eshop') }
                    <TShirtIcon className={classNames(classes.textPrimary, classes.ml3)} size={25}/>
                </Button>

                <div className={classNames(classes.textMono, classes.fontBold, classes.uppercase, classes.mt5, classes.underline)}>
                    Zobrazit vyzvy
                </div>
            </div>
        );
    }

    renderCartItems = ()=> {
        const { products } = this.props.doby.cart.data;
        return products.map(item => {
            const { product } = item;
            return (
                <CartItem 
                    key={product._id}
                    productId={product._id}
                    productName={product.name}
                    priceWithTax={product.price_with_tax}
                    thumbnailId={product.thumbnailId}
                    productCount={item.count}
                    countInStock={product.count}
                    catalogId={product.catalog_id}
                    challengeRef={product.challengeRef && product.challengeRef.id}
                    //techParams={product.techParams}
                />
            );
        });
    }

    getTotalPrice = ()=> {
        let price = 0;
        const { products } = this.props.doby.cart.data;
        for(const item of products) {
            price += (item.count * item.product.price_with_tax);
        }
        return formatPrice(price);
    }

    renderCart = ()=> {
        const { classes } = this.props;
        return ( 
            <>
                { this.renderCartItems() }
                <div className={classNames(classes.bottomSection)}>
                    <Button className={classNames(classes.mt4, classes.mb4)}
                        onClick={this.goToCheckout}
                        fullwidth
                    >
                        {this.props.translate('checkout')}
                    </Button>                   

                    {/* <h5 className={classNames(classes.textMd, classes.fontBold, classes.mt1)} style={{display: 'inline-block'}}>{this.props.translate('total_cost')}</h5>
                    <h5 className={classNames(classes.textMd, classes.mt1)} style={{display: 'inline-block', float: 'right', color: '#0e95d4'}}>{this.getTotalPrice()}{config.currency}</h5> */}

                    <div className={classNames(classes.fixedBottomBar, classes.py5)}>
                        <div className={classNames(classes.container)}>
                            <div className={classNames(classes.flexCol, classes.w100)}>                           
                                <div className={classNames(classes.dFlex, classes.mt2, classes.justifyContentBetween)}>
                                    <div className={classNames(classes.flexCol)}>
                                        <div className={classNames(classes.textMono, classes.textWhite, classes.fontSemibold)}>
                                            {this.props.translate('totalPrice')}
                                        </div>
                                        <div className={classNames(classes.textGray200, classes.textM, classes.mt1)}>
                                            {this.props.translate('with_tax')}
                                        </div>
                                    </div>
                                    <div className={classNames(classes.textMono, classes.textWhite, classes.fontSemibold)}>
                                        {this.getTotalPrice()}
                                    </div>
                                </div>          
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }

    render() {
        const { classes } = this.props;
        const productCount = this.getCartProductCount();

        return (
            <>
                <div className={classNames(classes.container, classes.mt5)}>
                    <Grid
                        container
                        direction="row"
                    >
                        <Grid item xs={12} md={5} lg={4} className={classNames()}>
                            {/* <h4 className={classNames(classes.uppercase, classes.textCenter, classes.mb4)}>
                                {this.props.translate('cart')} {productCount > 0 ? `( ${productCount} )` : ``}
                            </h4> */}
                            { productCount > 0 ? this.renderCart() : this.renderEmptyCart() }
                        </Grid>

                    </Grid>                    
                </div>                
            </>
        );
    }
}

Cart.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(
    withRouter,
    withStore,
    withStyles(styles),
    withDoby,
    withUser,
    withLocalization
)(Cart);