import React from "react";
import MailContext from "../contexts/MailContext";

const withMail = (WrappedComponent) => {
  return (props) => {
    return (
      <MailContext.Consumer>
        {(mail) => <WrappedComponent {...props} mail={mail} />}
      </MailContext.Consumer>
    );
  };
};

export default withMail;
