import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function CloseIcon(props) {
    const { classes } = props;
    return (
        <>
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}} onClick={()=> props.onClick && props.onClick()}>
                <path d="M7.5 7.5L16 16M24.5 24.5L16 16M16 16L24.5 7.5M16 16L7.5 24.5" stroke="currentColor" strokeWidth="1.5"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(CloseIcon);