import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import withStore from '../../../consumers/withStore';
import withDoby from '../../../consumers/withDoby';
import withUser from '../../../consumers/withUser';
import compose from '../../../compose';
import { withStyles } from '@material-ui/core/styles';
import { withLocalization } from '../../shared/localization'
import getSharedStyles from '../../../style';
import classNames from 'classnames';
//import config from '../../../config';
//import ClearIcon from '@material-ui/icons/Clear';
import { formatPrice/*, getCurrencySymbol*/ } from '../../../utils/helpers';

import MinusIcon from '../icons/Minus';
import PlusIcon from '../icons/Plus';
import CloseIcon from '../icons/Close';

const styles = theme => ({
    ...getSharedStyles(theme),
    // productThumbnail: {
    //     width: '70px'
    // },
    itemCountBtn: {
        border: `1px solid ${theme.palette.secondary.main}`,
        width: '24px',
        height: '24px',           
        cursor: 'pointer',
        color: theme.palette.secondary.main,
        display: 'flex',
        alignItems: 'center', 
        justifyContent: 'center'
    },
    itemCount: {
        width: '45px',
        textAlign: 'center',
    },
    fixedBottomBar: {
        position: 'fixed',
        bottom: '0',
        left: '0',
        width: '100%',
        background: '#F8F8F8',
        zIndex: 1,
    },
    thumbnailHolder: {
        height: '50px',
        width: '50px',              
        border: '1px solid #E4EAF3',
        padding: '3px'
    },
    productThumbnail: {
        objectFit: 'contain',
        position: 'absolute',
        top: '0',
        left: '0',
        width: '100%',
        height: '100%',
        zIndex: '0',
    },
    thumbnailSpacer: {
        padding: '5px',
        overflow: 'hidden',
        position: 'relative',
        height: '100%',
        width: '100%'
    }
});

function CartItem(props) {
    const { classes } = props;
    const renderVariants = () => {
        return (
            <div className={classNames(classes.textXs2)}>Variant</div>
        );
    }

    const [img, setImg] = useState(null);
    const [challenge, setChallenge] = useState(null);

    const removeCartItem = () => props.doby.cartRemoveProduct(props.productId);
    const increaseItemCount = (increase) => props.doby.cartIncreaseCount(props.productId, increase);

    useEffect(() => {
        const getImage = async (currentChallange = null) => {
            try {
                //console.log(currentChallange);
                if(props.thumbnailId || currentChallange) {
                    const data = await props.store.getFile(currentChallange ? currentChallange.logoDarkId : props.thumbnailId);
                    var reader = new FileReader();
                    reader.onloadend = () => {
                        setImg(reader.result);
                    }
                    reader.readAsDataURL(data);
                }
            } catch(e) {
                console.error(e);
            }
        }
        
        const getChallenge = async () => {
            const challenges = await props.doby.getChallenges();
            const currentChallange = challenges.data.find(x => x._id === props.challengeRef);
            setChallenge(currentChallange);
            getImage(currentChallange);
        }

        const fetchData = async() => {
            if(props.challengeRef) {
                await getChallenge();
                return;
            }
        }

        fetchData();
        getImage();
    }, [props.challengeRef, props.doby, props.store, props.thumbnailId]);

    return (
        <div className={classNames(classes.flexCol)}>
            <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter, classes.justifyContentBetween)}>
                <div className={classNames(classes.dFlex, classes.w100, classes.alignItemsCenter)}>
                    <div className={classNames(classes.thumbnailHolder)}>
                        <div className={classNames(classes.thumbnailSpacer)}>
                            <img
                            className={classes.productThumbnail}
                            src={img || ''} alt="" />
                        </div>
                    </div>

                    <div className={classNames(classes.flexCol, classes.ml3)}>
                        <div className={classNames(classes.textMono, classes.fontSemibold)}>{challenge ? challenge.name : props.productName}</div>
                        <div className={classNames(classes.textGray400, classes.textM)}>{renderVariants()}</div>
                    </div>
                </div>
                <CloseIcon onClick={removeCartItem} size={23}/>
            </div>
            <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.justifyContentBetween, classes.mt3)}>
                <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                    { props.catalogId !== "pack" && <>
                    <div className={classNames(classes.itemCountBtn)} onClick={() => increaseItemCount(-1)}><MinusIcon size={15}/></div>
                    <div className={classNames(classes.itemCount, classes.textM)}>{props.productCount}ks</div>
                    <div className={classNames(classes.itemCountBtn)} onClick={() => increaseItemCount(1)}><PlusIcon size={15}/></div>
                    </> }
                    <div className={classNames(classes.ml4, classes.textM, classes.textGray400)}>{props.productCount <= props.countInStock ? props.translate('in_stock') : props.translate('out_stock')}</div>
                </div>
                <div className={classNames(classes.flexCol)}>
                    <div className={classNames(classes.fontSemibold, classes.textMono)}>{formatPrice(Number(props.priceWithTax) * Number(props.productCount))}</div>
                    <div className={classNames(classes.textS, classes.textGray400, classes.textRight, classes.fontMedium)}>{props.translate('with_tax')}</div>
                </div>
            </div>
            <hr className={classNames(classes.w100, classes.mt4, classes.mb4, classes.hrLine)} />
        </div>
    );
}

CartItem.propTypes = {
    productName: PropTypes.string.isRequired,
    techParams: PropTypes.array,
    priceWithTax: PropTypes.number.isRequired,
    thumbnailId: PropTypes.string,
    productId: PropTypes.string.isRequired,
    catalogId: PropTypes.string.isRequired,
    productCount: PropTypes.number.isRequired,
    countInStock: PropTypes.number.isRequired,
    challengeRef: PropTypes.string,
};

export default compose(
    withStyles(styles),
    withDoby,
    withStore,
    withUser,
    withLocalization
)(CartItem);