import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}
            >
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M27 1v3m0 3V4m0 0h-3m3 0h3M13 6h-2L8.5 8v3l1.5 2m3-7v10m0-10h6m0 0h2l2.5 2v3L22 13m-3-7v10m-9-3h12m-12 0v5m12-5v5m0 0h2l1 1v3l-1 1h-2m0-5v5m0 0v1l-2 2h-8l-2-2v-1m0 0H8l-1-1v-3l1-1h2m0 5v-5"
                ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);