import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function PlusIcon(props) {
    const { classes } = props;
    return (
        <>          
            <svg width="100%" height="100%" viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg" className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}>
                <path d="M15.7783 4.07129V16.0921M15.7783 28.1129V16.0921M15.7783 16.0921L27.7991 16.0921M15.7783 16.0921H3.75751" stroke="currentColor" stroke-width="1.5"/>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(PlusIcon);