import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import ClearIcon from "@material-ui/icons/Clear";
import IconButton from "@material-ui/core/IconButton";
import Grid from "@material-ui/core/Grid";
import Select from "../shared/Select";

import classNames from "classnames";
import withDoby from "../../consumers/withDoby";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = (theme) => ({
  p10: {
    padding: "10px",
  },
});

class TechParamForm extends React.Component {
  getEnumValuesData = (enumType) => {
    const { enums } = this.props.store;
    return enumType ? enums.data.filter((e) => e.enumTypeId === enumType) : [];
  };

  render() {
    const {
      classes,
      enumTypes,
      enumType,
      enumValue,
      onChange,
      onDelete,
      index,
    } = this.props;

    const enumValues = this.getEnumValuesData(enumType);
    return (
      <Grid container direction="row" justify="left" alignItems="left">
        <Grid item xs={5} className={classNames(classes.p10)}>
          <Select
            value={enumType || ""}
            field="enumType"
            onChange={(name, value) => onChange(name, value, index)}
            data={enumTypes}
            textField="name"
            fullWidth
          />
        </Grid>
        <Grid item xs={6} className={classNames(classes.p10)}>
          <Select
            value={enumValue || ""}
            field="enumValue"
            onChange={(name, value) => onChange(name, value, index)}
            data={enumValues || []}
            textField="name"
            fullWidth
            disabled={enumValues.length === 0}
          />
        </Grid>
        <Grid item xs={1} className={classNames(classes.p10)}>
          <IconButton
            aria-label="Clear"
            className={classes.margin}
            onClick={() => onDelete(index)}
          >
            <ClearIcon fontSize="small" />
          </IconButton>
        </Grid>
      </Grid>
    );
  }
}

TechParamForm.propTypes = {
  classes: PropTypes.object.isRequired,
  onDelete: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  enumTypeData: PropTypes.array.isRequired,
  index: PropTypes.number.isRequired,
  enumTypes: PropTypes.array.isRequired,
  enumType: PropTypes.number.isRequired,
  enumValue: PropTypes.number.isRequired,
};

export default compose(
  withStyles(styles),
  withStore,
  withDoby,
  withUser
)(TechParamForm);
