import React/*, { useState, useEffect }*/ from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../../style';
import classNames from 'classnames';
import { withLocalization } from '../../shared/localization'
import compose from '../../../compose';
import CheckoutStepper from '../../dobyComponents/CheckoutStepper'
import Grid from '@material-ui/core/Grid';
import Button from '../../dobyComponents/form/Button';
import withDoby from '../../../consumers/withDoby';
import Faq from "../../dobyComponents/Faq";
import ProductSmallCard from '../../dobyComponents/eshop/ProductSmallCard';
//import config from '../../../config';
import Modal from "../../dobyComponents/Modal";
import CloseIcon from "../../dobyComponents/icons/Close";
import ProductDetail from "../../eshop/ProductDetail";

const styles = theme => ({
    ...getSharedStyles(theme),
    stepNumber: {
        height: '40px',
        width: '40px',
        minWidth: '40px',
        textAlign: 'center',
        fontWeight: '700',
        fontSize: '20px',
        lineHeight: '40px',
        background: theme.palette.secondary.main,
        color: 'white',
        borderRadius: '25px'
    },
    '@media screen and (max-width: 330px)': {
        productSmallCardHolder: {
            maxWidth: '100%',
            flexBasis: '100%'
        },
    },
    '@media screen and (min-width: 331px)': {
        productSmallCardHolder: {
            "&:nth-of-type(2n+1)": {
                paddingRight: "2px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "2px",
            },
        },
    },
    '@media screen and (min-width: 430px)': {
        productSmallCardHolder: {
            "&:nth-of-type(2n+1)": {
                paddingRight: "5px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "5px",
            },
        },
    },
    productModalClose: {
        position: "fixed",
        top: "15px",
        right: "15px",
        cursor: "pointer",
        zIndex: "10",
        [theme.breakpoints.up("sm")]: {
            position: "absolute",
            top: "30px",
            right: "30px",
        },
    }
    // productSmallCardContainer: {
    //     "& $productSmallCardHolder": {
    //         "&:nth-of-type(2n+1)": {
    //             paddingRight: "10px",
    //         },
    //         "&:nth-of-type(2n+2)": {
    //             paddingLeft: "10px",
    //         },
    //     },        
});

class ChallengeEquipment extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            challange: null,
            productDetailModalOpen: false,
            productDetailCatalogId: null,
        }
    }

    fetchData = async ()=> {
        const { _id } = this.props.match.params;

        await this.props.doby.getChallenges();
        const challenge = this.props.doby.challenges.data.find(x => x._id === _id);

        if (challenge) {
            this.setState({challenge});
        }

        //NOTE: get products & posts
        const filter = [
            { field: "main", value: true },
        ];

        await this.props.doby.getProducts(filter);
        await this.props.doby.getPosts([{ field: "type.key", value: 10 }], {
            field: "title",
            dir: "asc",
        });
    }

    componentDidMount() {
        this.fetchData();
    }

    nextStep = () => this.props.history.push(`/eshop/checkout-delivery-payment`);

    handleCloseProductDetailModal = () => {
        this.setState({productDetailModalOpen: false});
    }

    openProductModal = (catalogId) => {
        this.setState({productDetailModalOpen: true, productDetailCatalogId: catalogId});
    }

    renderProducts = () => {
        const { products } = this.props.doby;
        const { classes } = this.props;

        let uniqueProducts = [];      
        products.data.forEach(product => {
            let found = false;
            uniqueProducts.forEach(item=>{
                if(product.catalog_id === item.catalog_id) {
                    found = true;
                    return;
                }
            })
            if(!found) uniqueProducts.push(product)
        })

        return uniqueProducts.map(product => {
            return (
                <Grid item xs={6} sm={3} lg={2} key={product._id} className={classNames(classes.productSmallCardHolder, classes.mb3)}>
                    <ProductSmallCard
                        key={product._id}
                        title={product.name}
                        price={product.price_with_tax}
                        thumbnailId={product.thumbnailId}
                        catalogId={product.catalog_id}
                        onClickViewDetails={() => this.openProductModal(product.catalog_id)}
                        onClickAddCart={()=> this.openProductModal(product.catalog_id)}
                    />
                </Grid>
            );
        })
    }

    render() {
        const { classes } = this.props;
        return (
            <>
                <CheckoutStepper className={classNames(classes.mt5)} currentState={2} />
    
                <div className={classNames(classes.bgLight)}>
                    <div className={classNames(classes.container)}>
                        <Grid
                            container
                            direction="row"
                            justify="flex-start"
                        >
                            <Grid item xs={12} className={classNames(classes.mt7, classes.mb6)}>
                                <div className={classNames(classes.dFlex, classes.alignItemsCenter)}>
                                    <div className={classNames(classes.stepNumber)}>
                                        2
                                    </div>
                                    <div className={classNames(classes.dFlex, classes.flexCol, classes.ml4)}>
                                        <h3 className={classNames(classes.uppercase)}>
                                            { this.props.translate('challenge_checkout.equipment')}
                                        </h3>
                                        <div className={classNames(classes.textMono, classes.fontMedium, classes.textGray600)}>
                                            { this.props.translate('challenge_checkout.partofcommunity')}
                                        </div>
                                    </div>
                                </div>
                            </Grid>
                        </Grid>
                    </div>
                </div>
    
                <div className={classNames(classes.bgLight, classes.pb7)}>
                    <div className={classNames(classes.container)}>
                        <Grid
                            container
                            direction="row"
                            justify="flex-start"
                            className={classNames(classes.productSmallCardContainer)}
                        >
                            { this.renderProducts() }
                        </Grid>
    
                        <Grid
                            container
                            direction="row"
                            justify="flex-start"
                            className={classNames()}
                        >
                            <Grid item xs={12} className={classNames(classes.mt4)}>
                                <Button
                                    className={classNames()}
                                    fullwidth
                                    onClick={this.nextStep}
                                    arrow
                                >
                                    { this.props.translate('continue') }
                                </Button>
                            </Grid>
                        </Grid>
                    </div>
                </div>
    
                <div className={classNames(classes.container, classes.pb10)}>
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                    >
                        <Grid item xs={12} className={classNames(classes.mt8)}>
                            <h3 className={classNames(classes.uppercase)}>
                                { this.props.translate('questions.title') }
                            </h3>
                            <div className={classNames(classes.textGray500, classes.mt1, classes.mb5)}>
                                { this.props.translate('questions.sub_title') }
                            </div>
                            <Faq questions={this.props.doby.posts.data} />
                        </Grid>
                    </Grid>
                </div>
    
                <Modal
                    isOpen={this.state.productDetailModalOpen}
                    wrapperClassName={classNames(
                        classes.p0,
                        classes.challengeModalWrapper
                    )}
                    onClose={this.handleCloseProductDetailModal}
                >
                    <div
                        className={classNames(classes.productModalClose)}
                        onClick={this.handleCloseProductDetailModal}
                    >
                        <CloseIcon size={32} />
                    </div>
                    <div className={classNames(classes.pt5)}>
                        <ProductDetail
                            inModal
                            catalogId={this.state.productDetailCatalogId}
                        />
                    </div>
                </Modal>
            </>
        )
    }
}

export default compose(
    withStyles(styles),
    withLocalization,
    withDoby,
)(ChallengeEquipment);