import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>            
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}
                onClick={()=> props.onClick && props.onClick()}
            >
                <path
                    stroke="currentColor"
                    strokeWidth="1.5"
                    d="M16.959 8.625l-1.449-.388-3.804 2.604-1.94 7.245-2.612 1.841.386.668 3.38.906M16.96 8.625l.259-.966.612-.353.966.258.354.613-.26.966m-1.931-.518l1.932.518m0 0l1.449.388 1.991 4.157-1.94 7.245 1.34 2.9-.668.386-3.38-.906M10.92 21.5l-.431 1.665 1.331 1.416 3.456.921 1.955-.538.45-1.652M10.921 21.5l6.761 1.812M13.512 4.957L8.206 8.351l-1.025 3.825M23.775 7.934l2.8 5.339-1.036 3.863"
                ></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);