import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import compose from '../../compose';
import CircularProgress from '@material-ui/core/CircularProgress';

const styles = theme => ({
    ...getSharedStyles(theme),
    circleLoaderSvg: {
        color: theme.palette.primary.main,
    }
});

function CircleLoader(props) {
    const { classes } = props;
    return (
        <>
            <div className={classNames(classes.dFlex, props.center && classes.justifyContentCenter, props.className)}>
                <CircularProgress className={classes.progress} classes={{svg: classes.circleLoaderSvg}}/>  
            </div>   
        </>
    )
}

export default compose(
    withStyles(styles),
)(CircleLoader);