import React from "react";
import PropTypes from "prop-types";
import FormHelperText from "@material-ui/core/FormHelperText";
import { withStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import ListItemText from "@material-ui/core/ListItemText";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import Checkbox from "@material-ui/core/Checkbox";
import { withLocalization } from "../../shared/localization";
import classNames from "classnames";
import FilledInput from "@material-ui/core/FilledInput";

const styles = (theme) => ({
    selectInput: {
        background: "white!important",
        border: "1px solid #C8D2E1",
        borderRadius: "0px",
        "&:before": {
            display: "none",
        },
        "&:after": {
            display: "none",
        },
    },
    inputLabel: {
        transform: 'translate(12px, 17px) scale(1)'
    },
    selectRoot: {
        background: "white!important",
        padding: "23px 12px 8px",
        fontSize: "0.875rem",
        color: "#333442",
    },
});

function SelectFieldComponent(props) {
    const {
        classes,
        data,
        textField,
        required,
        value,
        onChange,
        field,
        multiple,
    } = props;
    if (
        required &&
        (value === null || value === undefined || value === "") &&
        data &&
        data.length > 0
    ) {
        onChange(field, data[0]._id);
    }

    const getText = (selected) => {
        const value = data.find((x) => x._id === selected);
        return value ? value[textField] : null;
    };

    const renderValue = (selected) => {
        if (!multiple) {
            return getText(selected);
        }

        return selected.map((s) => getText(s)).join(" | ");
    };

    const label = props.label || props.translate("field." + props.field);
    return (
        <FormControl
            className={classNames(props.className)}
            required={required}
            fullWidth={props.fullWidth}
            variant="filled"
            error={props.errorText && props.errorText !== ""}
        >
            {label && (
                <InputLabel
                    htmlFor={props.field}
                    className={classNames(classes.textGray400, classes.textM, classes.textMono, classes.inputLabel)}
                >
                    {label}
                </InputLabel>
            )}
            <Select
                value={props.value !== null ? props.value : ""}
                onChange={(e) => onChange(props.field, e.target.value)}
                id={props.field}
                multiple={props.multiple}
                renderValue={renderValue}
                disabled={props.disabled}
                input={<FilledInput name={props.field} id={props.field} />}
                className={classes.selectInput}
                classes={{ select: classes.selectRoot }}
            >
                {!props.required && !multiple && (
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                )}
                {data.map((x) => (
                    <MenuItem key={x._id} value={x._id}>
                        {multiple && (
                            <Checkbox
                                checked={value.find((d) => d === x._id) !== undefined}
                            />
                        )}
                        <ListItemText primary={x[textField]} />
                    </MenuItem>
                ))}
            </Select>
            {props.errorText && <FormHelperText className={classNames(classes.textError)}>{props.errorText}</FormHelperText>}
        </FormControl>
    );
}

SelectFieldComponent.propTypes = {
    className: PropTypes.string,
    classes: PropTypes.object.isRequired,
    label: PropTypes.string,
    field: PropTypes.string.isRequired,
    value: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    required: PropTypes.bool,
    data: PropTypes.array.isRequired,
    textField: PropTypes.string.isRequired,
    fullWidth: PropTypes.bool,
    multiple: PropTypes.bool,
    disabled: PropTypes.bool,
    errorText: PropTypes.string,
};

export default withLocalization(withStyles(styles)(SelectFieldComponent));
