import React from "react";
import PropTypes from "prop-types";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";

import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../../shared/localization";
import getSharedStyles from "../../../style";

import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";

import classNames from "classnames";

const styles = (theme) => ({
  ...getSharedStyles(theme),
    textInput:{
        width: '100%!important',
        background: "white!important",
        border: "1px solid #C8D2E1!important",
        borderRadius: '0px!important',
        height: '52px!important',
        fontFamily: '"IBM Plex Sans", sans-serif!important',
        fontSize: "0.875rem!important",
        paddingLeft: '68px!important'
    },
    dropdownButton: {
        padding: '0 10px!important',
        background: '#F8F9FB!important',
        border: "1px solid #C8D2E1!important",
        borderRadius: '0px!important',
        '& .selected-flag ': {
            background: '#F8F9FB!important',
        }
    }
});

function TelFieldComponent(props) {
  const { classes } = props;
  //const label = props.label || props.translate("field." + props.field);

  return (
    <FormControl
      className={classNames(props.className)}
      variant="filled"
      fullWidth={props.fullWidth}
      required={props.required}
      error={props.errorText && props.errorText !== ""}
    >     
      <PhoneInput
        country={props.country && props.country.split("-")[0]}
        value={props.value || ""}
        onChange={(phone) => props.onChange(props.field, phone)}
        inputClass={classes.textInput}
        buttonClass={classes.dropdownButton}
        countryCodeEditable={false}
        preferredCountries={['sk', 'pl', 'cz', 'at', 'de' ]}
        regions={'europe'}
      />
      {props.errorText && <FormHelperText className={classNames(classes.textError)}>{props.errorText}</FormHelperText>}
    </FormControl>
  );
}

TelFieldComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
  label: PropTypes.string,
  field: PropTypes.string.isRequired,
  value: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  placeholder: PropTypes.string,
  fullWidth: PropTypes.bool,
  required: PropTypes.bool,
  type: PropTypes.string,
  disabled: PropTypes.bool,
  multiline: PropTypes.bool,
  rows: PropTypes.number,
  startAdornment: PropTypes.string,
  onBlur: PropTypes.func,
  errorText: PropTypes.string,
  width: PropTypes.number,
  country: PropTypes.string,
};

export default withLocalization(withStyles(styles)(TelFieldComponent));
