import React, { useState,useEffect  } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import { getStyles } from '../shared/style';
import compose from '../../compose';
import { withRouter } from 'react-router';
import withStore from '../../consumers/withStore';

const styles = theme => ({
    ...getSharedStyles(theme),
    ...getStyles(theme),
    partnerImg: {
        width: '100%'
    }
});

function PartnerCard(props) {
    const { classes } = props;
    const [img, setImg] = useState(null);

    useEffect(() => {  
        const getImg = async () =>{
            try {       
                if(props.logoId) {
                    const data = await props.store.getFile(props.logoId);
                    var reader = new FileReader();
                    reader.onloadend = () => {
                        setImg(reader.result);
                    }
                    reader.readAsDataURL(data);
                }
            } catch(e) {
                console.error(e);
            }
        }   
        if (!img) {
            getImg();
        }
    }, [img, props.logoId, props.store]);
 
    return (
        <div>           
            {img && <img src={img} className={classes.partnerImg} alt={props.name}/>}
        </div>
    );
}

PartnerCard.propTypes = {
    name: PropTypes.string,
    desc: PropTypes.string,
    logoId: PropTypes.string.isRequired,   
};

export default compose(
    withRouter,
    withStyles(styles),
    withStore
)(PartnerCard)