import React from "react";
import Table from "../shared/table/Table";

import TooltipImage from "./TooltipImage";
import compose from "../../compose";
import Button from "@material-ui/core/Button";
import withDoby from "../../consumers/withDoby";
import { withStyles } from "@material-ui/core/styles";
import { withLocalization } from "../shared/localization";
import { getStyles } from "../shared/style";
import withUser from "../../consumers/withUser";
import withMail from "../../consumers/withMail";
import config from "../../config";

const styles = (theme) => ({
  ...getStyles(theme),
  tooltip: {
    "max-width": "150px",
    "max-height": "150px",
  },
});

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: null,
      isOpen: false,
    };
  }

  sendMail = async (id, challengeName, summitName) => {
    let subject = this.props.translate("email_climbVerified_subject");
    subject = subject.replace("{0}", challengeName);

    const data = {
      subject: subject.replace("{1}", summitName),
      from: config.mail.address,
      fromName: config.mail.name,
      recipientRef: {
        id,
      },
      ru: "/climb/" + id,
      templateId: config.mail.climbVerifiedTemplate,
      params: [challengeName, summitName],
    };

    await this.props.mail.sendMail(data);
  };

  handleVerifyClimb = async (x) => {
    const { verifyClimb, refreshClimbs } = this.props.doby;

    await verifyClimb(x._id);
    refreshClimbs();

    this.sendMail(x._id, x.challenge.name, x.summit.name);
  };

  verifyButton = (x) => {
    const { translate } = this.props;

    return (
      <Button
        variant="contained"
        color="primary"
        disabled={x.verified !== null}
        onClick={() => this.handleVerifyClimb(x)}
      >
        {translate("verify")}
      </Button>
    );
  };

  tooltipImage = (x) => {
    const { classes } = this.props;
    return <TooltipImage classes={classes} data={x} />;
  };

  generateRows = () => {
    return [
      { field: "description", disablePadding: true },
      { field: "created", type: "date" },
      { field: "verified", type: "date" },
      { field: "_img", cell: this.tooltipImage },
      { field: "_id" },
      { field: "verify", cell: this.verifyButton },
    ];
  };

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  };

  handleClose = () => {
    this.setState({ isOpen: false });
  };

  handleOpen = () => {
    this.setState({ isOpen: true });
  };

  handleChange = async (filters, page, sort, render) => {
    try {
      return await this.props.doby.getClimbs(filters, sort, page, render);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  onEdit = async (x) => {
    this.setState({ x, isOpen: true });
  };

  isDisabled = (x) => {
    return true;
  };

  render() {
    const { translate } = this.props;

    return (
      <div>
        <Table
          rows={this.generateRows()}
          data="climbs"
          onChange={this.handleChange}
          title={translate("climbs")}
          onEdit={this.onEdit}
          store={this.props.doby}
          filters={[]}
          isDisabled={this.isDisabled}
        />
      </div>
    );
  }
}

export default compose(
  withDoby,
  withUser,
  withMail,
  withLocalization,
  withStyles(styles)
)(TableWrapper);
