import React from "react";
import PropTypes from "prop-types";

import compose from "../../compose";
import withDoby from "../../consumers/withDoby";
import { withStyles } from "@material-ui/core/styles";
import { getStyles } from "../shared/style";
import Tooltip from "@material-ui/core/Tooltip";
import ImageIcon from "@material-ui/icons/Image";

const styles = (theme) => ({
  ...getStyles(theme),
  tooltip: {
    "max-width": "150px",
    "max-height": "150px",
  },
});

class TooltipImage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      _img: null,
    };
  }

  callBack = (name, value) => {
    this.setState({ [name]: value });
  };

  getImage = () => {
    if (this.state._img) return;
    this.props.doby.getImageClimb(this.props.data, this.callBack, true);
  };

  render() {
    const { classes } = this.props;
    return (
      <Tooltip
        title={
          <img
            src={this.state._img}
            alt="Attached"
            className={classes.tooltip}
          />
        }
        onOpen={this.getImage}
      >
        <ImageIcon />
      </Tooltip>
    );
  }
}

TooltipImage.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object.isRequired,
};

export default compose(withDoby, withStyles(styles))(TooltipImage);
